.img-content {
  position: relative;
}

.img-content img {
  position: absolute;
  width: 80%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.content-col {
  padding: 7% 15%;
  box-sizing: border-box;
  text-align: center;
  background: #fff;
}

.heading {
  font-size: 35px;
  margin-bottom: 40px;
  text-transform: uppercase;
  color: #333333;
  font-weight: 900;
  letter-spacing: 2px;
}

.desc {
  font-size: 30px;
  color: #888686;
  font-weight: 300;
  margin-bottom: 0;
}

@media (min-width: 1201px) and (max-width: 1500px) {
  .heading {
    font-size: 30px;
    margin-bottom: 28px;
    margin-bottom: 25px;
  }

  .desc {
    font-size: 22px;
    line-height: 32px;
  }
}

@media (min-width: 768px) and (max-width: 1200px) {
  .heading {
    font-size: 25px;
    margin-bottom: 28px;
    margin-bottom: 20px;
  }

  .desc {
    font-size: 20px;
    line-height: 25px;
  }
}

@media (min-width: 577px) and (max-width: 767px) {
  .heading {
    font-size: 22px;
    margin-bottom: 28px;
    margin-bottom: 20px;
  }

  .desc {
    font-size: 18px;
    line-height: 24px;
  }
}

@media (max-width: 575px) {
  .heading {
    font-size: 20px;
    margin-bottom: 24px;
    margin-bottom: 15px;
  }

  .desc {
    font-size: 16px;
    line-height: 19px;
  }
}
