.what-we-do-sec {
  padding: 3%;
  background: #e7eef6;
}

.what-we-do-div {
  display: inline-block;
  width: 44%;
  height: 44%;
  padding: 32px;
  text-align: center;
  background: #fff;
  border-bottom: 5px solid #0378a6;
  margin: 3%;
  vertical-align: top;
}

.what-we-do-div img {
  width: 50%;
  margin-bottom: 10px;
  /* border-radius: 50%;
  border: 2px solid #fff; */
  padding: 15px;
}

.what-we-do-div .title {
  font-size: 20px;
  color: #333333;
  margin-bottom: 0;
}

.what-we-do-div .sub-title {
  font-size: 16px;
  color: #0378a6;
  margin-bottom: 0;
}

@media (max-width: 575px) {
  .what-we-do-div {
    padding: 15px 10px;
  }

  .what-we-do-div img {
    width: 65%;
    margin-bottom: 0;
  }

  .what-we-do-div .title {
    font-size: 18px;
  }

  .what-we-do-div .sub-title {
    font-size: 15px;
  }
}

@media (min-width: 576px) and (max-width: 991px) {
  .what-we-do-sec {
    padding: 5% 12%;
  }
}

@media (min-width: 992px) and (max-width: 1440px) {
  .what-we-do-sec {
    padding: 6% 3%;
  }

  .what-we-do-div {
    padding: 35px 10px;
  }
  .what-we-do-div img {
    width: 65%;
    margin-bottom: 0;
  }

  .what-we-do-div .title {
    font-size: 18px;
  }

  .what-we-do-div .sub-title {
    font-size: 15px;
  }
}
