.contact-div {
  padding: 3% 8% 3% 0;
  background: #e7eef6;
}

.enquery-div {
  padding: 10% 5%;
  background: #fff;
  color: #000;
  height: 100%;
}

.enquery-div .heading {
  font-size: 25px;
  text-align: center;
  font-weight: 600;
  color: #03738c;
  margin-bottom: 25px;
}

.enquery-div .submit-btn {
  display: block;
  background: #0378a6;
  color: #fff;
  width: 70%;
  margin: auto;
  margin-top: 25px;
  margin-bottom: 25px;
}

.enquery-div .submit-btn:hover {
  background: #043e55;
}

.enquery-div .note {
  font-size: 14px;
  color: #636363;
  margin-bottom: 0;
}

.form-control {
  border: 1px solid #ccc;
}

.form-control:focus {
  outline: 0;
  box-shadow: none;
  border-color: #03738c;
}

.form-control.is-invalid,
.was-validated .form-control:invalid {
  background-image: none;
}

.form-control.is-invalid:focus,
.was-validated .form-control:invalid:focus {
  box-shadow: none;
}

.form-group select {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: textfield;
  background: url("data:image/svg+xml;utf8,<svg fill='black' height='24' viewBox='0 0 24 24' width='24' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>")
    #f2f2f2 no-repeat calc(100% - 5px) !important;
}

.form-group label {
  margin-bottom: 3px;
  color: #333333;
}

@media (max-width: 575px) {
  .contact-div {
    padding: 0% 5% 8% 5%;
  }

  .enquery-div .heading {
    font-size: 20px;
  }
}

@media (min-width: 576px) and (max-width: 991px) {
  .contact-div {
    padding: 0% 5% 8% 5%;
  }

  .enquery-div .heading {
    font-size: 20px;
  }
}

@media (min-width: 992px) and (max-width: 1440px) {
  .contact-div {
    padding: 3% 4.5% 3% 0;
    background: #e7eef6;
  }
}
