.parax-div {
  height: 50vh;
}

.expertise-div {
  position: absolute;
  background: #021113c2;
  padding: 30px;
  width: 500px;
  top: 50%;
  left: 23%;
  transform: translate(-50%, -50%);
  border-radius: 15px;
  opacity: 0.9;
}

.expertise-div p {
  font-size: 40px;
  line-height: 52px;
  color: #ffffff;
  font-weight: 400;
  margin-bottom: 0;
}

@media (min-width: 1201px) and (max-width: 1500px) {
  .parax-div {
    height: 35vh;
  }

  .expertise-div {
    padding: 22px;
    width: 350px;
    top: 50%;
    left: 24%;
  }

  .expertise-div p {
    font-size: 30px;
    line-height: 36px;
  }
}

@media (min-width: 768px) and (max-width: 1200px) {
  .parax-div {
    height: 30vh;
  }

  .expertise-div {
    padding: 22px;
    width: 300px;
    top: 50%;
    left: 24%;
  }

  .expertise-div p {
    font-size: 25px;
    line-height: 30px;
  }
}

@media (min-width: 577px) and (max-width: 767px) {
  .parax-div {
    height: 30vh;
  }

  .expertise-div {
    padding: 22px;
    width: 238px;
    top: 50%;
    left: 24%;
  }

  .expertise-div p {
    font-size: 20px;
    line-height: 26px;
  }
}

@media (max-width: 575px) {
  .parax-div {
    height: 30vh;
  }

  .expertise-div {
    padding: 20px;
    width: 238px;
    top: 50%;
    left: 40%;
  }

  .expertise-div p {
    font-size: 18px;
    line-height: 24px;
  }
}
